/*Mobile First*/


.container-menu {
  display: grid;
}

.hamburger-react {
  text-align: center;
  margin-top: 1rem;
  margin-right: -1.5rem;
  grid-column: 5/6;
  grid-row: 1/2;
}
.navbar-logo {
  grid-column: 1/4;
  grid-row: 1/2;
}
.logo-menu {
  padding-left: 1.2rem;
  width: 6rem;
  height: 5.5rem;
}

.container-menu nav {
  text-decoration: none;
  grid-column: 1/6;
  grid-row: 2/3;
}

.active {
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  list-style: none;
  font-size: 1.5rem;
}

.active a {
  text-decoration: none;
}

li {
  padding: 0 0 0.5rem 0;
}

.hidden {
  display: none;
}

.styledicon {
  color: #2c3761;
  width: 1.5rem;
  height: 1.5rem;
}

.styledcart-mobile {
  padding-top: 27px;
  grid-column: 4/5;
  grid-row: 1/2;
}


.styledcart {
  display: none;
} 

/* @1200 px */

@media screen and (min-width: 1200px) {
  .container-menu {
    width: 100%;
    display: flex;
  }

  nav {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }

  .hidden {
    font-size: 1.5rem;
    padding: 0;
    margin: 0;
    width: 70%;
    display: flex;
    justify-content: space-between;
    list-style: none;
  }

  .hamburger-react {
    display: none;
  }

  .logo-menu {
    padding-left: 1.2rem;
    grid-column: 1/2;
    grid-row: 1/2;
    width: 7.5rem;
    height: 7rem;
  }

  .styledcart-mobile {
    display: none;
  }

  .styledcart {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    margin-right: 1rem;
    grid-column: 4/5;
    grid-row: 1/2;
  }

  .hidden a {
    text-decoration: none;
  }

  .styledicon {
    color: #2c3761;
    width: 1.5rem;
    height: 1.5rem;
  }
}

@media screen and (min-width: 900px) and (max-width: 1200px) {
  .hamburger-react {
    display: none;
  }

  .logo-menu {
    width: 6.5rem;
    height: 6rem;
  }

  .container-menu nav {
    text-decoration: none;
    grid-column: 2/4;
    grid-row: 1/2;
  }

  .styledcart-mobile {
    display: none;
  }

  .styledcart {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    margin-right: 1rem;
    grid-column: 4/5;
    grid-row: 1/2;
  }

  nav {
    display: flex;
  }

  .hidden {
    width: 100%;
    padding: 0;
    margin: inherit;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    list-style: none;
    font-size: 1.1rem;
  }

  .hidden a {
    text-decoration: none;
  }

  .styledicon {
    color: #2c3761;
    width: 1.3rem;
    height: 1.3rem;
  }

  .styledcart {
    width: 1.1rem;
    height: 1.1rem;
  }
}

@media screen and (min-width: 786px) and (max-width: 900px) {
  .hamburger-react {
    display: none;
  }

  .logo-menu {
    width: 5rem;
    height: 4rem;
  }

  .container-menu nav {
    text-decoration: none;
    grid-column: 2/4;
    grid-row: 1/2;
  }

  .styledcart-mobile {
    display: none;
  }

  .styledcart {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    margin-right: 1rem;
    grid-column: 4/5;
    grid-row: 1/2;
  }

  nav {
    display: flex;
  }

  .hidden {
    width: 100%;
    padding: 0;
    margin: inherit;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    list-style: none;
    font-size: 1.1rem;
  }

  .hidden a {
    text-decoration: none;
  }

  .styledicon {
    color: #2c3761;
    width: 1.3rem;
    height: 1.3rem;
  }

  .styledcart {
    width: 1.1rem;
    height: 1.1rem;
  }
}

@media screen and (min-width: 480px) and (max-width: 786px) {
  .hamburger-react {
    margin-right: -5.5rem;
  }
}


